import React, { useEffect, useState, useRef, forwardRef, useImperativeHandle } from 'react';
import "leaflet/dist/leaflet.css";
import L from "leaflet";
import './floor_map.css';

import desk from './desk.png'


interface plotInfo {
  latitude: any
  longitude: any
}


interface LeafletComponent {
  plotInfo: plotInfo
  rowData?: any
  Icons?: any
  zoomControl?: string
  floorInfo: any
}



const LeafletComponent = (props: LeafletComponent) => {
  let { rowData, zoomControl, plotInfo, floorInfo } = props;
  let { longitude, latitude } = plotInfo;
  const popup = L.popup()
  const [floorBound, setFloorBound] = useState([])
  const mapContainerRef: any = useRef(null);
  const mapIElement: any = useRef(null);
  let map: any = mapIElement.current;
  var featureGroup = L.featureGroup();
  // const [rowData,setRowData] = useState([]);

  const [isFloor, setisFloor] = useState(false)
  var shipLayer = L.layerGroup();




  useEffect(() => {
    initMap()

    setFloor()
    plotIcon()
    return () => {
      removeMap()
    }
  }, [plotInfo,floorInfo,rowData]);


  let initMap=()=>{
    let zoomvalue=10
    map = L.map(mapContainerRef.current, {
      center: [latitude, longitude],
      minZoom: zoomvalue-1,
      maxZoom: zoomvalue+1,
      zoom: zoomvalue,
      zoomSnap: 0.15,
      maxBoundsViscosity: 1,
      //maxBounds: L.latLngBounds([[(latitude ), (longitude)], [(latitude ), (longitude)]])
    });
    map.zoomControl.setPosition(zoomControl || 'topleft')
    map.addLayer(shipLayer);
    const popup = L.popup()
    // function onMapClick(e: any) {
    //   popup
    //     .setLatLng(e.latlng)
    //     .setContent(`You clicked the map at ${e.latlng.toString()}`)
    //     .openOn(map);
    // }



    // map.on('click', onMapClick);


  }
  let removeMap=()=>{
    map.remove();
  }











  // useEffect(() => {

  //   setFloor()
  // }, [floorInfo]);


  const setFloor = () => {
    let srcWidth=floorInfo.srcWidth
    let srcHeight=floorInfo.srcHeight;
    const { innerWidth: width, innerHeight: height } = window;
    var ratio = Math.min(width / srcWidth, height / srcHeight);
    let newImg_w=srcWidth*ratio/1000;
    let newImg_h=srcHeight*ratio/1000
    var latLngBounds = L.latLngBounds([latitude, longitude], [newImg_h,newImg_w]);
    
    let fllormapLaer= L.imageOverlay(floorInfo.img, latLngBounds, {
      opacity: 0.9,
    }) .addTo(map);
   
    

    map.setView(map.options.center, map.options.zoom);
    map.fitBounds(latLngBounds);
    shipLayer.clearLayers();
 
  }





  useEffect(() => {
    plotIcon()
  }, [rowData]);


  let plotIcon = () => {

    L.marker([0.466613, 0.525032]).addTo(map)
    rowData.forEach((element: any) => {
     
      switch ( element.typeOf) {
        case 'chair':
          chairIocn(element)
          break;
          case 'room':
            roomIcon(element)
          break;
        default:
          break;
      }


     
     
    });

  }

  let chairIocn=(element:any)=>{
    if(element.isBooked  ){
      let {profile_pic,name ,designation,mobile}=element.bookingInfo[0]
      const popupContent =` <div class='icon-popup'>
      <div class='title'>  <span> booked </span>
      <span> ${new Date()}</span>
      </div>
      <div  class='info'>
      <img src='${profile_pic}' />
     <div class="info-details">  
     <span> ${element.title} </span>
     <span> ${ name}</span>
     <span class='designation'> ${ designation}</span>
     <span class='mobile'> ${ mobile}</span>
     </div>
     
      </div>
      </div>`
            let latLngBounds = L.latLngBounds([[element.Bounds[0], element.Bounds[1]], [element.Bounds[0]+0.04, element.Bounds[1]+0.04]]);
                let chairIocn=  L.imageOverlay(element?.img,
                latLngBounds, {
                  opacity: 1,
                  className:"chair booked",
                  interactive: true
                }) //.addTo(map)
                .bindTooltip(element.title, {permanent: false, className: "icon-label", offset: [20, 0] })
                .bindPopup(popupContent)
   
  	shipLayer.addLayer(chairIocn)
                
                
  

               
             
             

    }
    else{
      
      let latLngBounds = L.latLngBounds([[element.Bounds[0], element.Bounds[1]], [element.Bounds[0]+0.04, element.Bounds[1]+0.04]]);
      let chairIocn=  L.imageOverlay(element?.img,
      latLngBounds, {
      opacity: 1,
      className:"chair",
      //interactive: true
    })
    //.addTo(map)
    .bindTooltip(element.title, {permanent: false, className: "icon-label", offset: [20, 0] })
    
  	shipLayer.addLayer(chairIocn)
    }
  
  }

  let roomIcon=(element:any)=>{

    if(element.isBooked  ){
      let {profile_pic,name ,designation,mobile}=element.bookingInfo[0]
      const popupContent =` <div class='icon-popup'>
      <div class='title'>  <span> booked </span>
      <span> ${new Date()}</span>
      </div>
      <div  class='info'>
      <img src='${profile_pic}' />
     <div class="info-details">  
     <span> ${element.title} </span>
     <span> ${ name}</span>
     <span class='designation'> ${ designation}</span>
     <span class='mobile'> ${ mobile}</span>
     </div>
     
      </div>
      </div>`
            let latLngBounds = L.latLngBounds(element.Bounds);
                  L.imageOverlay(element?.img,
                latLngBounds, {
                  opacity: 0.7,
                  className:"room booked",
                  interactive: true
                }).addTo(map).bindTooltip(element.title, {permanent: true, className: "icon-label", offset: [0, 0] })
                .bindPopup(popupContent)
          

    }
    else{
      
      let latLngBounds = L.latLngBounds(element.Bounds);
      L.imageOverlay(element?.img,
    latLngBounds, {
      opacity: 0.7,
      className:"room",
      //interactive: true
    }).addTo(map).bindTooltip(element.title, {permanent: true, className: "icon-label", offset: [0, 0] })

    }

    
  }


  return (
    <div className="floor_map-container" ref={mapContainerRef} />
  );
}

export default LeafletComponent;